.pointer {
  width: 0.1em;
  height: calc(1.3 * var(--text-font-size));
  background-color: orange;
  position: absolute;
  transition: left 0.1s;
}
.fade {
  animation: fade 1s ease-out alternate-reverse infinite;
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
